.c-menu {
  display: block;
  position: fixed;
  z-index: ($z-index-modal + 1);

  @media screen and (max-width: 31.4375em) {
    top: $size-xsmall;
    right: $size-xsmall;
    color: white;

    &:hover,
    &:active {
      color: white;
    }

    [data-menu=open] & {
      color: $navigation-color--text;
    }
  }

  @media screen and (min-width: 31.5em) {
    @include ui-link($background: true);
    top: 0;
    right: 0;
    padding: $space-medium;
    box-shadow: inset 0 -1px 0 $navigation-color--offset;
  }

  @media print {
    display: none;
  }
}

.c-menu__line {
  transform-origin: 50% 50%;

  // Transitions
  &:nth-of-type(1),
  &:nth-of-type(2),
  &:nth-of-type(5),
  &:nth-of-type(6) {
    opacity: 1;
    transition: transform 0.2s cubic-bezier(0.8, 0, 0.4, 1.8) 0.2s, opacity 0.05s linear 0.1s;

    [data-menu=open] & {
      opacity: 0;
      transition: transform 0.2s ease-out, opacity 0.05s linear 0.1s;
    }
  }

  &:nth-of-type(3),
  &:nth-of-type(4) {
    transition: transform 0.2s ease-out;

    [data-menu=open] & {
      transition: transform 0.2s cubic-bezier(0.8, 0, 0.4, 1.8) 0.2s;
    }
  }

  // Transforms
  [data-menu=open] & {
    &:nth-of-type(1) {
      transform: translateY(500%);
    }

    &:nth-of-type(2) {
      transform: translateY(250%);
    }

    &:nth-of-type(3) {
      transform: rotate(45deg) scaleX(1.3334);
    }

    &:nth-of-type(4) {
      transform: rotate(-45deg) scaleX(1.3334);
    }

    &:nth-of-type(5) {
      transform: translateY(-250%);
    }

    &:nth-of-type(6) {
      transform: translateY(-500%);
    }
  }
}
